:root {
  --reflex-prefix: ;
  --reflex-columns: 12;
  --reflex-grid-spacing: 15px;
  --reflex-xs: 576px;
  --reflex-sm: 768px;
  --reflex-md: 992px;
  --reflex-lg: 1200px;
  --reflex-xlg: 1600px;
  --reflex-xxs-max: 575px;
  --reflex-xs-max: 767px;
  --reflex-sm-max: 991px;
  --reflex-md-max: 1199px;
  --reflex-lg-max: 1599px;
}
p.h1,
p.h2,
p.h3,
p.h4,
p.h5,
p.h6 {
  font-family: "Foundry Gridnik W01";
}
.lead {
  font-size: 21px;
  line-height: 1.71;
}
small,
.small {
  font-size: 12px;
}
a.button,
main button[type="submit"] {
  position: relative;
  display: inline-block;
  font-family: "Crimson Text";
  font-weight: 700;
  font-size: 18px;
  line-height: 1.222em;
  padding: 0.889em;
  padding-bottom: 10px;
  cursor: pointer;
  color: #06262D;
  color: var(--dark-green);
  outline: 2px solid;
  outline-color: #F4C55C;
  outline-color: var(--button-default);
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
  transform-style: preserve-3d;
  letter-spacing: 0.06em;
}
a.button:hover,
main button[type="submit"]:hover,
a.button:focus,
main button[type="submit"]:focus,
a.button.hovered,
main button[type="submit"].hovered {
  outline-color: #80C7BC;
  outline-color: var(--button-hover);
  background-color: inherit;
  outline-width: 5px;
  text-decoration: none;
}
a.button:hover:before,
main button[type="submit"]:hover:before,
a.button:focus:before,
main button[type="submit"]:focus:before,
a.button.hovered:before,
main button[type="submit"].hovered:before {
  transform: translateX(0);
}
p > a.button,
p > main button[type="submit"] {
  margin-top: 1.333em;
}
a.button {
  margin-top: 3em;
}
a.call-to-action {
  font-family: "Foundry Gridnik W01";
  font-weight: 500;
  font-size: 26px;
  line-height: 1.15;
  font-variant: small-caps;
  text-transform: lowercase;
  position: relative;
  display: inline /* Makes the underline follow the length of text, even when multi-line */;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0) 50%, #F4C55C 50%) /* The animating underline */;
  background-size: 200% 2px /* The underline has twice the width of text; */;
  background-position: 0 100% /* but offset to the left to be hidden */;
  background-repeat: repeat-x;
  transition: background-position 0s;
  box-shadow: inset 0 -2px 0 #F4C55C /* The static line that disappears on hover */;
}
a.call-to-action:hover,
a.call-to-action:focus,
a.call-to-action.hovered {
  box-shadow: none;
  background-position: -100% 100%;
  transition-duration: 0.5s;
}
.centered {
  text-align: center;
  text-align-last: center;
}
img.cover {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 992px) {
  ul.two-column {
    column-count: 2;
    column-gap: 3em;
  }
  ul.two-column li {
    break-inside: avoid-column;
  }
  ul.two-column li:first-of-type {
    margin-top: 0;
  }
}
